@import 'normalize.css';

:root {
  --primary: #fe2c55;
  --black: #000;
  --white: #fff;
  --text-color: #161823;

  --font-family: 'IBM', sans-serif;
  // Default layout
  --default-layout-header-height: 60px;
  --default-layout-width: 1150px;
  --default-layout-horizontal-spacer: 24px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}
body {
  font-family: var(--font-family);
  font-size: 1.6rem;
  line-height: 1.5;
  text-rendering: optimizeSpeed;
  color: var(--text-color);
  overflow-y: overlay;
}

// Scrollbar CSS

html ::-webkit-scrollbar {
  border-radius: 0px;
  width: 8px;
}
html ::-webkit-scrollbar-thumb {
  border-radius: 4px;
  // background-color: rgba(22, 24, 35, 0.06);
  background-color: rgba(22, 24, 35, 0.15);
}
html ::-webkit-scrollbar-track {
  border-radius: 0px;
  background-color: rgba(0, 0, 0, 0);
}

button,
input,
[tabindex] {
  outline: none;
  border: none;
}

a {
  color: var(--text-color);
  text-decoration: none;
}

//Fonts embedded
@font-face {
  font-family: IBM;
  src: url('/assets/fonts/IBM_Plex_Sans/IBMPlexSans-Regular.ttf') format(openType);
  font-weight: 400;
}
@font-face {
  font-family: IBM;
  src: url('/assets/fonts/IBM_Plex_Sans/IBMPlexSans-SemiBold.ttf') format(openType);
  font-weight: 600;
}
@font-face {
  font-family: IBM;
  src: url('/assets/fonts/IBM_Plex_Sans/IBMPlexSans-Bold.ttf') format(openType);
  font-weight: 700;
}

// Custom tippy tooltip
body .tippy-box {
  border-radius: 8px;
  font-weight: 600;
  font-style: 1.6rem;
  line-height: 22px;
  background-color: rgba(84, 84, 84, 0.92);

  .tippy-arrow {
    color: rgba(84, 84, 84, 0.92);
  }

  .tippy-content {
    padding: 8px 9px;
  }

  .tippy-box[data-placement^='bottom'] > .tippy-arrow::before {
    top: -8px;
  }
}
